/* Mobile-first styling */

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  padding: 20px;
  text-align: center;
}

.app-title {
  font-size: 2rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 20px;
  animation: fadeIn 2s ease-in-out;
}

.app-description {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 20px;
}

.response-container {
  width: 100%;
  max-width: 600px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  animation: slideUp 1.5s ease-in-out;
}

.station-card {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 15px;
  margin-bottom: 15px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.station-card:hover {
  transform: translateY(-5px);
  background-color: #f0f8ff;
}

.station-name {
  font-size: 1.5rem;
  color: #ea5a28;
  margin-bottom: 5px;
}

.station-address {
  font-size: 1rem;
  color: #555;
  margin-bottom: 10px;
  text-decoration: underline;
  text-decoration-color: blue;
}

.station-prices {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 1rem;
  color: #333;
}

.station-prices p {
  width: 48%;
  margin: 5px 0;
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
}

.loading-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  animation: pumpAnimation 1s infinite alternate;
}

.loading-text {
  font-size: 1.2rem;
  color: #555;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes pumpAnimation {
  0% { transform: scale(1); }
  100% { transform: scale(1.2); }
}

@keyframes float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Floating icons */
.floating-icons {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.floating-icon {
  font-size: 3rem;
  position: absolute;
  animation: float 10s infinite;
  opacity: 0.5;
}

.floating-icon:nth-child(1) {
  animation: moveAcrossScreen 15s linear infinite, float 10s infinite;
  top: 10%;
  left: 0%;
}

.floating-icon:nth-child(2) {
  animation: moveAcrossScreen 20s linear infinite, float 10s infinite;
  top: 50%;
  left: -10%;
}

.floating-icon:nth-child(3) {
  animation: moveAcrossScreen 18s linear infinite, float 10s infinite;
  top: 80%;
  left: -15%;
}

/* Modal styling */
.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 400px;
  height: auto;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  pointer-events: auto;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  width: 100%;
  box-sizing: border-box;
}

.modal-buttons {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.modal-buttons button {
  margin: 10px 0;
  padding: 15px 20px;
  font-size: 18px;
  cursor: pointer;
  width: 100%;
  box-sizing: border-box;
}

/* Responsive adjustments */
@media (min-width: 768px) {
  .app-title {
    font-size: 2.5rem;
  }

  .response-container {
    max-width: 800px;
  }

  .station-card {
    display: flex;
    flex-direction: column;
  }

  .station-name {
    font-size: 1.8rem;
  }

  .station-address {
    font-size: 1.2rem;
  }

  .station-prices p {
    width: 22%;
    margin: 10px 0;
  }
}
